<template>
   <div>
       <Headers :title="title" />
       <div class="infos">
           <img src="https://avatars3.githubusercontent.com/u/25030274?s=460&v=4">
           <p>
               <span>{{username}}</span>
               <span>余额：￥{{yu}}</span>
           </p>
       </div>
       <ul class="info-detail">
       <router-link to="/log">
           <li>
              投注记录
              <i class="right_icon" />
           </li>
        </router-link>
        <router-link to="/usi">
           <li>
              实名信息
              <i class="right_icon" />
           </li>
        </router-link>
           <li>
              新手上路
              <i class="right_icon" />
           </li>
       </ul>
   </div>
</template>
<script>
import Headers from '../common/Header'
export default{
    data(){
        return{
            title:'用户中心',
            username:'',
            yu:0.00
        }
    },
    created(){
        this.$nextTick(()=>{
           this.username=this.$store.state.userinfo['username'];
           this.yu=this.$store.state.userinfo['yu'];
        }) 
    },
    components:{
        Headers
    }
}
</script>
<style>
    .info-detail{
        margin-top:0.4rem;
        padding:0 0.2rem;
        background:#fff;
    }
    .info-detail li{
        height:1.33rem;
        line-height:1.33rem;
        border-bottom:1px solid #e2e2e2;
        position:relative;
        font-size:0.38rem;
    }
    .infos{
        margin-top:50px;
        padding:0.5rem 0.2rem;
        background-image:url('data:image/png;base64,');
        background-repeat: repeat-y;
        background-size: contain;
    }
    .infos img{
        height:1.2rem;
        width:1.2rem;
        border-radius:50%;
        vertical-align:bottom;
    }
    .infos p{
        width:4rem;
        display:inline-block;
    }
    .infos p span{
        display:inline-block;
        width:100%;
        color:#fff;
    }
    .infos p span:nth-of-type(1){
         font-size:0.38rem;
    }
    .infos p span:nth-of-type(2){
         font-size:0.32rem;
    }
</style>


